import { Component, OnInit } from '@angular/core';
import { VoucherService } from '../../../common/voucher/voucher.service';
import { Observable } from 'rxjs/Observable';
import { UserService } from 'app/common/user/user.service';
import { Router } from '@angular/router';

declare var jquery: any;
declare var $: any;

@Component({
  selector: 'app-main-deals',
  templateUrl: './main-deals.component.html',
  styleUrls: ['./main-deals.component.css']
})
export class MainDealsComponent implements OnInit {

  private eventwork: number;
  private vouchers: any = [];
  private categories: any = [];
  private alive: boolean = true;
  currentPage = 0;
  category = null;
  selectedCategory = null;
  pick = null;
  totalProducts = 0;
  bannerDeals = 'deals-banner.jpg';

  constructor(private userService: UserService, private router: Router, private voucherService: VoucherService) {
    this.eventwork = 1;
  }

  ngOnInit() {
    if (window.screen.width <= 959) {
      this.bannerDeals = 'deals-banner-xs.jpg';
    }
    this.getCategories();
    this.getVouchers();
  }

  changeCategory(category: any) {
    this.selectedCategory = category;
    this.category = category.categoryCode;
    this.getVouchers();

  }

  ngOnDestroy() {
    this.alive = false;
  }

  lookupAsync = (query: string): Observable<any[]> => {
    if (!query) {
      return null;
    }
    return this.userService.search(query).map(response => response.vouchers);
  }
  gotoProduct(code: any) {
    this.router.navigate(["/voucher/details-voucher/", code]);
  }
  changed() {
    this.gotoProduct(this.pick.id);
  }

  getCategories() {
    this.voucherService.getCategories().subscribe((r) => {
      this.categories = r;
    })
  }

  getVouchers() {
    this.vouchers = [];
    this.voucherService.getVouchers(this.category, '4', this.currentPage, 9, 1).subscribe(
      res => {
        for (var i in res) {
          this.voucherService.getVoucherDetail(res[i].voucherVendorLocationCode).takeWhile(() => this.alive).subscribe(
            res => {
              this.vouchers.push(res);
              if (this.vouchers && this.vouchers[0]) {
                this.totalProducts = this.vouchers[0].totalCount;
              }
            });
        }
      });
  }

  onChangeEvent(event: number, selected: string) {

    var $container = $('.albumContainer');
    $container.isotope({
      filter: '*',
      animationOptions: {
        duration: 750,
        easing: 'linear',
        queue: false
      }
    });
    $('.albumFilter li').on('click', function () {
      var selector = $(this).attr('data-filter');
      $container.isotope({
        filter: selector,
        animationOptions: {
          duration: 750,
          easing: 'linear',
          queue: false
        }
      });
      return false;
    });

    this.eventwork = event;

  }

}



